<template>
  <div class="login-container">
    <!-- model	表单数据对象 -->
    <!-- rules	表单验证规则 -->
    <!-- autocomplete 自动补全输入 -->
    <!-- label-position	表单域标签的位置 -->
    <el-form
      ref="loginForm"
      :model="loginInfo"
      :rules="loginRules"
      class="login-form"
      autocomplete="on"
      label-position="left"
    >
      <!-- 登录标题 -->
      <div class="title-container">
        <h3 class="title">账户登录</h3>
      </div>

      <!-- 用户名输入 -->
      <el-form-item prop="username">
        <span class="svg-container">
          <svg-icon icon-class="user" />
        </span>
        <el-input
          ref="username"
          v-model="loginInfo.username"
          placeholder="输入用户名"
          name="username"
          type="text"
          tabindex="1"
          autocomplete="on"
        />
      </el-form-item>

      <!-- 密码输入 -->
      <el-tooltip v-model:visible="capsTooltip" content="Caps lock is On" placement="right" manual>
        <el-form-item prop="password">
          <span class="svg-container">
            <svg-icon icon-class="password" />
          </span>
          <el-input
            :key="passwordType"
            ref="password"
            v-model="loginInfo.password"
            :type="passwordType"
            placeholder="输入密码"
            name="password"
            tabindex="2"
            autocomplete="on"
            @keyup="checkCapslock"
            @blur="capsTooltip = false"
            @keyup.enter="handleLogin"
          />
          <span class="show-pwd" @click="showPwd">
            <svg-icon :icon-class="passwordType === 'password' ? 'eye' : 'eye-open'" />
          </span>
        </el-form-item>
      </el-tooltip>

      <!-- 验证码输入 -->
      <el-form-item prop="captcha" class="captcha-input">
        <span class="svg-container">
          <svg-icon icon-class="message" />
          <!-- <el-icon><PictureFilled /></el-icon> -->
        </span>
        <el-input
          ref="captcha"
          v-model="loginInfo.captcha"
          placeholder="输入验证码"
          name="captcha"
          type="text"
          tabindex="3"
          @keyup.enter="handleLogin"
        />
      </el-form-item>
      <img
        class="captcha-img"
        :src="captchaUrl"
        @click="getCaptchaImg()"
        alt="图片加载失败"
        title="验证码有效期1分钟"
      />

      <!-- 登录按钮 -->
      <el-button
        :loading="loading"
        class="login-button"
        type="primary"
        @click.prevent="handleLogin"
      >
        登 录
      </el-button>

      <el-checkbox v-model="rememberMe" style="margin-bottom: 20px" @change="handleCookies()"
        >记住我</el-checkbox
      >

      <!-- 其他信息 -->
      <div style="position: relative">
        <div class="tips">
          <span>其他登录方式：</span>
        </div>
        <social-sign style="display: inline" />
        <a @click="forgetPwd" class="register-link" style="right: 70px"> 忘记密码 </a>
        <a @click="handleRegister" class="register-link" style="right: 0"> 立即注册 </a>
        <!-- <el-button class="thirdparty-button" type="primary" @click="showDialog = true">
          Or connect with
        </el-button> -->
      </div>
    </el-form>

    <!-- <el-dialog title="Or connect with" v-model="showDialog">
      Can not be simulated on local, so please combine you own business simulation! ! !
      <br />
      <br />
      <br />
      <social-sign />
    </el-dialog> -->
  </div>
</template>

<script>
import { validUsername } from "@/utils/validate"
import SocialSign from "./components/SocialSignin"
import { getCaptchaImage } from "@/api/user"
import Cookies from "js-cookie"

export default {
  name: "Login",
  components: { SocialSign },
  data() {
    const validateUsername = (rule, value, callback) => {
      if (!validUsername(value)) {
        callback(new Error("Please enter the correct user name"))
      } else {
        callback()
      }
    }
    const validatePassword = (rule, value, callback) => {
      if (value.length < 6) {
        callback(new Error("The password can not be less than 6 digits"))
      } else {
        callback()
      }
    }
    return {
      loginInfo: { username: "admin", password: "111111", captcha: "" },
      loginRules: {
        username: [{ required: true, trigger: "blur", validator: validateUsername }],
        password: [{ required: true, trigger: "blur", validator: validatePassword }],
      },
      passwordType: "password",
      capsTooltip: false,
      loading: false,
      showDialog: false,
      redirect: undefined,
      otherQuery: {},
      captchaUrl: "",
      rememberMe: false, //保存登录信息
    }
  },
  watch: {
    $route: {
      handler: function (route) {
        const query = route.query
        if (query) {
          this.redirect = query.redirect
          this.otherQuery = this.getOtherQuery(query)
        }
      },
      immediate: true,
    },
  },
  created() {
    // window.addEventListener('storage', this.afterQRScan)
  },
  mounted() {
    // 从Cookie中读取布尔值要注意类型转换
    this.rememberMe = Cookies.get("REMEMBER_ME") == "true" ? true : false
    if (this.rememberMe) {
      const username = Cookies.get("USER_NAME")
      const password = Cookies.get("USER_PWD")
      if (username) this.loginInfo.username = username
      if (password) this.loginInfo.password = password
    }
    if (this.loginInfo.username === "") {
      this.$refs.username.focus()
    } else if (this.loginInfo.password === "") {
      this.$refs.password.focus()
    } else if (this.loginInfo.captcha === "") {
      this.$refs.captcha.focus()
    }
    this.getCaptchaImg() //获取验证码图片
  },
  unmounted() {
    // window.removeEventListener('storage', this.afterQRScan)
  },
  methods: {
    checkCapslock(e) {
      const { key } = e
      this.capsTooltip = key && key.length === 1 && key >= "A" && key <= "Z"
    },
    showPwd() {
      if (this.passwordType === "password") {
        this.passwordType = ""
      } else {
        this.passwordType = "password"
      }
      this.$nextTick(() => {
        this.$refs.password.focus()
      })
    },
    handleCookies() {
      if (this.rememberMe) {
        Cookies.set("USER_NAME", this.loginInfo.username, { expires: 30 })
        Cookies.set("USER_PWD", this.loginInfo.password, { expires: 30 })
      } else {
        Cookies.remove("USER_NAME")
        Cookies.remove("USER_PWD")
      }
      Cookies.set("REMEMBER_ME", this.rememberMe, { expires: 30 })
      // this.$forceUpdate()
    },
    // 登录
    handleLogin() {
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          this.loading = true
          this.$store
            .dispatch("user/login", this.loginInfo)
            .then(() => {
              this.$router.push({ path: this.redirect || "/", query: this.otherQuery })
              this.loading = false
            })
            .catch(() => {
              this.loading = false
              this.loginInfo.captcha = "" // 清空验证码输入框
              this.getCaptchaImg() // 重新获取验证码
            })
        } else {
          console.log("error submit!!")
          return false
        }
      })
    },
    // 注册
    handleRegister() {
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          // this.loading = true
          this.$store
            .dispatch("user/register", this.loginInfo)
            .then(() => {
              alert("注册成功！")
              this.$router.push({ path: this.redirect || "/", query: this.otherQuery })
              // this.loading = false
            })
            .catch(() => {
              // this.loading = false
              this.loginInfo.captcha = "" // 清空验证码输入框
              this.getCaptchaImg() // 重新获取验证码
            })
        } else {
          console.log("error submit!!")
          return false
        }
      })
    },
    // 获取验证码图片
    getCaptchaImg() {
      getCaptchaImage()
        .then((response) => {
          return (
            "data:image/jpeg;base64," +
            btoa(
              new Uint8Array(response.data).reduce(
                (data, byte) => data + String.fromCharCode(byte),
                ""
              )
            )
          )
        })
        .then((data) => {
          this.captchaUrl = data //赋值给img标签的src属性
        })
    },
    getOtherQuery(query) {
      return Object.keys(query).reduce((acc, cur) => {
        if (cur !== "redirect") {
          acc[cur] = query[cur]
        }
        return acc
      }, {})
    },
    // afterQRScan() {
    //   if (e.key === 'x-admin-oauth-code') {
    //     const code = getQueryObject(e.newValue)
    //     const codeMap = {
    //       wechat: 'code',
    //       tencent: 'code'
    //     }
    //     const type = codeMap[this.auth_type]
    //     const codeName = code[type]
    //     if (codeName) {
    //       this.$store.dispatch('LoginByThirdparty', codeName).then(() => {
    //         this.$router.push({ path: this.redirect || '/' })
    //       })
    //     } else {
    //       alert('第三方登录失败')
    //     }
    //   }
    // }
  },
}
</script>

